@use 'base/variables' as v;
@use 'base/mixins' as m;

/* footer */

.footer{
    background-color: darken($color: v.$primario, $amount: 30%);
    padding: 5rem 0 1rem 0;

    &__contenido{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include m.tablet { 
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__copyright{
        color: v.$blanco;
        text-align: center;
        font-size: 1.4rem;
        font-weight: 900;
        border-top:2px solid v.$blanco ;
        padding: 2rem 0;
        max-width: 60%;
        margin: 0 auto;
        
    }
}