@use 'base/variables' as v;
@use 'base/mixins' as m;

/* logo */


.logo{
    display: flex;
    align-items: center;
    margin-bottom: 5rem;
    @include m.tablet { 
        margin-bottom: 0;
    }

    &__imagen{
        max-width: 5rem;
        max-height: 5rem;
    }
    &__nombre{
        @include m.logo;
    }
}