@use 'base/variables' as v;
@use 'base/mixins' as m;

/* navegacion */

.navegacion{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;

    @include m.tablet { 
        flex-direction: row;
    }
    
    

    &__link{
        color: v.$blanco;
        text-decoration: none;
        text-transform: uppercase;
        padding: .8rem;
        font-size: 1.4rem;
        font-weight: 700;
        
    }

    &__link:hover{
        background-color:v.$primario;
    }
    
}


