@use 'base/variables' as v;
@use 'base/mixins' as m;

/* nosotros */

.nosotros{
    margin-top: 5rem;
    padding-bottom: 5rem;

    &__heading{
        @include m.heading;
    }
    

    &__texto{
        padding: 0 1rem;
        text-align: center;
        line-height: 1.8;
        @include m.tablet { 
            text-align: left;
        }
    }
}