@use 'base/variables' as v;
@use 'base/mixins' as m;

/* viajes */

.viajes{
    padding:5rem 0;
    background-color: v.$gris;

    &__heading{
        @include m.heading;
    }

    &__grid{
        @include m.grid(1, 2rem);

        @include m.tablet { 
            @include m.grid(2, 2rem);
        }

        @include m.desktop { 
            @include m.grid(3, 2rem);
        }
    }

}

.viaje{
    &__grid{
        padding: 5rem 0;
        display: grid;
        gap: 2rem;
        @include m.tablet { 
            @include m.grid(2, 2rem);
        }
    }

 
    &__imagen{
        @include m.reajuste;
    }
    
    &__contenido{
        padding: 1rem 2rem;
        background-color: v.$blanco;
        
    }

    &__heading{
        font-weight: 700;
    }

    &__fecha{
        color: v.$primario;
        font-weight: 400;
        margin-left: .5rem;
        padding-left: .5rem;
        border-left: 5px solid v.$primario;
    }

    &__descripcion{
        display: -webkit-box;/*su forma de verse*/
        -webkit-box-orient: vertical;/*horientacion del texto, en este caso en vertical*/
        -webkit-line-clamp: 4;/*las lineas que debe cortar*/
        overflow: hidden;/* para contener el contenido */
        font-size: 1.6rem;
    }

    &__boton{
        @include m.boton(v.$primario, v.$blanco);
        display: block;
        margin: 5rem 0 3rem 0;
    }

    
}

.compra{
    background-color: darken($color: v.$primario, $amount: 8%);
    margin: 0;
    padding: 2rem;

    &__tabla-precio{
        display: flex;
        justify-content: space-between;
        
        border-bottom: 2px solid v.$blanco;
        
    }

    &__texto,
    &__precio{
        font-size: 2rem;
        font-weight: 700;
        color: v.$blanco;
        margin: 0;
        
        
    }

    &__heading{
        font-weight: 900;
        margin: 0;
        padding: 2rem 1rem;
        color: v.$blanco;
    }

    &__formulario{
        display: flex;
        flex-direction: column;
    }
    &__label{
        color: v.$blanco;
        font-weight: 700;
        line-height: 1.8;
        font-size: 1.8rem;
    }

    &__input{
        border: 2px solid v.$blanco;
        margin-bottom: 2rem;
        padding: 1rem;
        border-radius: 10rem;
    }
    &__boton{
        @include m.boton(v.$blanco, v.$negro);
        display: block;
        margin: 5rem 0 3rem 0;
        border-radius: 10rem;
    }
}

/* formulario */

.formulario{
    padding:5rem 0;
    

    &__heading{
        @include m.heading;
    }


}

.form{
    max-width: 80rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border: 2px solid v.$primario;
    padding: 5rem;

    &__campo{
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &__label{
        color: v.$primario;
        font-weight: 700;
        line-height: 1.8;
        font-size: 1.8rem;
    }

    &__input{
        border: 2px solid v.$primario;
        margin: 2rem 0;
        padding: 1rem;
        border-radius: 10rem;
    }
    &__submit{
        @include m.boton(v.$primario, v.$blanco);
        display: block;
        margin: 5rem 0 3rem 0;
        border-radius: 10rem;
    }
}