@use 'base/variables' as v;
@use 'base/mixins' as m;

/* testimoniales */

.testimoniales{
    padding: 5rem 0;

    &__heading{
        @include m.heading;
    }
    &__grid{
        @include m.grid(1, 2rem);

        @include m.tablet { 
            @include m.grid(2, 2rem);
        }
    }

}

.testimonial{
    
    padding: 2rem;
    background-color: v.$secundario;
    color: v.$negro;
    
    
    &__texto{
        margin-left: 4rem;
        margin-right: 4rem;
        position: relative;
        padding: 2rem;
        font-size: 1.6rem;
        line-height: 1.8;
        font-weight: 400;
        background-color: v.$blanco;
        box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);

        &::before{
            content: '"';
            display: block;
            width: 4rem;
            height: 4rem;
            font-size: 7rem;
            position: absolute;
            top: -4.5rem;
            left: -4.5rem;
            font-weight: 900;
            color: darken($color: v.$gris, $amount: 50%);
            
        }
        &::after{
            content: '"';
            display: block;
            width: 4rem;
            height: 4rem;
            font-size: 7rem;
            position: absolute;
            right: -5rem;
            bottom: 1rem;
            font-weight: 900;
            color: darken($color: v.$gris, $amount: 50%);
            
        }
        

        
        
    }

    &__autor{
        font-weight: 900;
        color: v.$blanco;
        font-size: 2rem;
        text-align: right;
        
    }
    
    

    
}