@use 'base/variables' as v;
@use 'base/mixins' as m;

/* header */

.header{
    background-image:linear-gradient(to right, rgb(0 0 0/ .5), rgb(0 0 0/ .7)), url(../img/header_bg.jpg);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    max-height: auto;
    padding: 5rem 0;
    


    &__flex{
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet { 
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }

    &__eslogan{
        text-align: center;
        padding: 5rem 0;
        @include m.tablet { 
            text-align: left;


        }

        animation-name: mostrarEslogan;
        animation-duration: 1s;
        animation-delay: 0;
        animation-timing-function: ease-in-out;
        animation-iteration-count: 1;
       
    }


    &__heading{
        color: v.$blanco;
        font-weight: 900;
        font-size: 4rem;
      
    }

    &__texto{
        color: v.$blanco;
        font-weight: 400;
        font-size: 1.8rem;

        @include m.tablet { 
            max-width: 50%;
        }
    }


    &__link{
        @include m.boton(v.$primario, v.$blanco);
    }
}

@keyframes mostrarEslogan{
    0%{
        opacity: 0;
        transform: translateX(-10rem);
        
    }

    50%{
        opacity: 0;
    }

    100%{
        opacity: 1;
        transform: translateX(0rem);
    }
}