@use 'variables' as v; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin desktopXL {
    @media (min-width: v.$desktopxl) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    margin-top: 2rem;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;

    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10% );
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin logo{
    font-weight: 900;
    
    color: v.$blanco;

    span{
        color: v.$primario;
    }

}

@mixin heading{
    color: darken($color: v.$primario, $amount: 20%);
    text-align: center;
    font-weight: 900;
    font-size: 4rem;
    margin-bottom: 5rem;
    width: 60%;
    margin: 0 auto 5rem auto;
    padding: 1rem;
    border-bottom: 1px solid darken($color: v.$negro, $amount: 10%);
}

@mixin reajuste{
   
    
    
    img{
        width: 100%;
        height: 40vh;
        object-fit: cover;
        

        
        
    }


}

@mixin imagenScale{
    img{
        object-fit: cover;
        height: 100%;
        transition-property:transform ;
        transition-duration: 300ms;
        

        &:hover{
            transform: scale(1.3)
        }
    }
}
