@use 'base/variables' as v;
@use 'base/mixins' as m;

/* galeria */

.galeria{
    margin-top: 5rem;
    padding:5rem 0;
    overflow: hidden;

    &__heading{
        @include m.heading;
    }

    &__grid{
        display: grid;
        @include m.grid(2, 1rem);
        overflow: hidden;

        @include m.imagenScale;
        

        @include m.tablet { 
            @include m.grid(3, 1rem);
        }

        
    }

    &__imagen{
        overflow: hidden;
    }

    &__imagen:nth-child(2){
        grid-column: 2 / 3;
        grid-row: 1 / 3;
    }
    
    
    &__imagen:nth-child(3){
        grid-row: 3 / 5;
        grid-column: 2 / 3;
    }
    &__imagen:nth-child(6){
        grid-row: 2 / 4;
    }

}