html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

strong {
  color: #214a37;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

/* header */
.header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.7)), url(../img/header_bg.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  max-height: auto;
  padding: 5rem 0;
}
.header__flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__flex {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__eslogan {
  text-align: center;
  padding: 5rem 0;
  animation-name: mostrarEslogan;
  animation-duration: 1s;
  animation-delay: 0;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
}
@media (min-width: 768px) {
  .header__eslogan {
    text-align: left;
  }
}
.header__heading {
  color: #FFFFFF;
  font-weight: 900;
  font-size: 4rem;
}
.header__texto {
  color: #FFFFFF;
  font-weight: 400;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .header__texto {
    max-width: 50%;
  }
}
.header__link {
  background-color: #40916C;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
}
@media (min-width: 768px) {
  .header__link {
    width: auto;
  }
}
.header__link:hover {
  background-color: #306e52;
}

@keyframes mostrarEslogan {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}
/* logo */
.logo {
  display: flex;
  align-items: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .logo {
    margin-bottom: 0;
  }
}
.logo__imagen {
  max-width: 5rem;
  max-height: 5rem;
}
.logo__nombre {
  font-weight: 900;
  color: #FFFFFF;
}
.logo__nombre span {
  color: #40916C;
}

/* navegacion */
.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion__link {
  color: #FFFFFF;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0.8rem;
  font-size: 1.4rem;
  font-weight: 700;
}
.navegacion__link:hover {
  background-color: #40916C;
}

/* nosotros */
.nosotros {
  margin-top: 5rem;
  padding-bottom: 5rem;
}
.nosotros__heading {
  color: #214a37;
  text-align: center;
  font-weight: 900;
  font-size: 4rem;
  margin-bottom: 5rem;
  width: 60%;
  margin: 0 auto 5rem auto;
  padding: 1rem;
  border-bottom: 1px solid black;
}
.nosotros__texto {
  padding: 0 1rem;
  text-align: center;
  line-height: 1.8;
}
@media (min-width: 768px) {
  .nosotros__texto {
    text-align: left;
  }
}

/* viajes */
.viajes {
  padding: 5rem 0;
  background-color: #f5f5f5;
}
.viajes__heading {
  color: #214a37;
  text-align: center;
  font-weight: 900;
  font-size: 4rem;
  margin-bottom: 5rem;
  width: 60%;
  margin: 0 auto 5rem auto;
  padding: 1rem;
  border-bottom: 1px solid black;
}
.viajes__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .viajes__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .viajes__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.viaje__grid {
  padding: 5rem 0;
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .viaje__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.viaje__imagen img {
  width: 100%;
  height: 40vh;
  object-fit: cover;
}
.viaje__contenido {
  padding: 1rem 2rem;
  background-color: #FFFFFF;
}
.viaje__heading {
  font-weight: 700;
}
.viaje__fecha {
  color: #40916C;
  font-weight: 400;
  margin-left: 0.5rem;
  padding-left: 0.5rem;
  border-left: 5px solid #40916C;
}
.viaje__descripcion {
  display: -webkit-box; /*su forma de verse*/
  -webkit-box-orient: vertical; /*horientacion del texto, en este caso en vertical*/
  -webkit-line-clamp: 4; /*las lineas que debe cortar*/
  overflow: hidden; /* para contener el contenido */
  font-size: 1.6rem;
}
.viaje__boton {
  background-color: #40916C;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0 3rem 0;
}
@media (min-width: 768px) {
  .viaje__boton {
    width: auto;
  }
}
.viaje__boton:hover {
  background-color: #306e52;
}

.compra {
  background-color: #347557;
  margin: 0;
  padding: 2rem;
}
.compra__tabla-precio {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid #FFFFFF;
}
.compra__texto, .compra__precio {
  font-size: 2rem;
  font-weight: 700;
  color: #FFFFFF;
  margin: 0;
}
.compra__heading {
  font-weight: 900;
  margin: 0;
  padding: 2rem 1rem;
  color: #FFFFFF;
}
.compra__formulario {
  display: flex;
  flex-direction: column;
}
.compra__label {
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1.8;
  font-size: 1.8rem;
}
.compra__input {
  border: 2px solid #FFFFFF;
  margin-bottom: 2rem;
  padding: 1rem;
  border-radius: 10rem;
}
.compra__boton {
  background-color: #FFFFFF;
  padding: 1.5rem 4rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0 3rem 0;
  border-radius: 10rem;
}
@media (min-width: 768px) {
  .compra__boton {
    width: auto;
  }
}
.compra__boton:hover {
  background-color: #e6e6e6;
}

/* formulario */
.formulario {
  padding: 5rem 0;
}
.formulario__heading {
  color: #214a37;
  text-align: center;
  font-weight: 900;
  font-size: 4rem;
  margin-bottom: 5rem;
  width: 60%;
  margin: 0 auto 5rem auto;
  padding: 1rem;
  border-bottom: 1px solid black;
}

.form {
  max-width: 80rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border: 2px solid #40916C;
  padding: 5rem;
}
.form__campo {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.form__label {
  color: #40916C;
  font-weight: 700;
  line-height: 1.8;
  font-size: 1.8rem;
}
.form__input {
  border: 2px solid #40916C;
  margin: 2rem 0;
  padding: 1rem;
  border-radius: 10rem;
}
.form__submit {
  background-color: #40916C;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0 3rem 0;
  border-radius: 10rem;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
  }
}
.form__submit:hover {
  background-color: #306e52;
}

/* testimoniales */
.testimoniales {
  padding: 5rem 0;
}
.testimoniales__heading {
  color: #214a37;
  text-align: center;
  font-weight: 900;
  font-size: 4rem;
  margin-bottom: 5rem;
  width: 60%;
  margin: 0 auto 5rem auto;
  padding: 1rem;
  border-bottom: 1px solid black;
}
.testimoniales__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.testimonial {
  padding: 2rem;
  background-color: #95D5B2;
  color: #000;
}
.testimonial__texto {
  margin-left: 4rem;
  margin-right: 4rem;
  position: relative;
  padding: 2rem;
  font-size: 1.6rem;
  line-height: 1.8;
  font-weight: 400;
  background-color: #FFFFFF;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
.testimonial__texto::before {
  content: '"';
  display: block;
  width: 4rem;
  height: 4rem;
  font-size: 7rem;
  position: absolute;
  top: -4.5rem;
  left: -4.5rem;
  font-weight: 900;
  color: #767676;
}
.testimonial__texto::after {
  content: '"';
  display: block;
  width: 4rem;
  height: 4rem;
  font-size: 7rem;
  position: absolute;
  right: -5rem;
  bottom: 1rem;
  font-weight: 900;
  color: #767676;
}
.testimonial__autor {
  font-weight: 900;
  color: #FFFFFF;
  font-size: 2rem;
  text-align: right;
}

/* blog */
.blog {
  padding: 5rem 0;
  background-color: #f5f5f5;
}
.blog__heading {
  color: #214a37;
