.entrada__imagen img {
  width: 100%;
  height: 40vh;
  object-fit: cover;
}
.entrada__contenido {
  padding: 1rem 2rem;
  background-color: #FFFFFF;
}
.entrada__heading {
  font-weight: 700;
}
.entrada__fecha {
  color: #40916C;
  font-weight: 400;
  margin-left: 0.5rem;
  padding-left: 0.5rem;
  border-left: 5px solid #40916C;
}
.entrada__descripcion {
  display: -webkit-box; /*su forma de verse*/
  -webkit-box-orient: vertical; /*horientacion del texto, en este caso en vertical*/
  -webkit-line-clamp: 4; /*las lineas que debe cortar*/
  overflow: hidden; /* para contener el contenido */
  font-size: 1.6rem;
}
.entrada__enlace {
  background-color: #40916C;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
