.entrada__enlace:hover {
  background-color: #306e52;
}

/* galeria */
.galeria {
  margin-top: 5rem;
  padding: 5rem 0;
  overflow: hidden;
}
.galeria__heading {
  color: #214a37;
  text-align: center;
  font-weight: 900;
  font-size: 4rem;
  margin-bottom: 5rem;
  width: 60%;
  margin: 0 auto 5rem auto;
  padding: 1rem;
  border-bottom: 1px solid black;
}
.galeria__grid {
  display: grid;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  overflow: hidden;
}
.galeria__grid img {
  object-fit: cover;
  height: 100%;
  transition-property: transform;
  transition-duration: 300ms;
}
.galeria__grid img:hover {
  transform: scale(1.3);
}
@media (min-width: 768px) {
  .galeria__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.galeria__imagen {
  overflow: hidden;
}
.galeria__imagen:nth-child(2) {
  grid-column: 2/3;
  grid-row: 1/3;
}
.galeria__imagen:nth-child(3) {
  grid-row: 3/5;
  grid-column: 2/3;
}
.galeria__imagen:nth-child(6) {
  grid-row: 2/4;
}

/* footer */
.footer {
  background-color: #11271d;
  padding: 5rem 0 1rem 0;
}
.footer__contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__contenido {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__copyright {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 900;
  border-top: 2px solid #FFFFFF;
  padding: 2rem 0;
  max-width: 60%;
  margin: 0 auto;
}